<template>
  <demo title="Icon" class="icon-demo">
    <demo-item name="default" description="" :code="code">
      <Btn @click.native="$smoothScroll(300)">300</Btn>
    </demo-item>
    <demo-item name="default" description="" :code="ele_code">
      <div ref="target"># Target</div>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <Btn @click.native="$smoothScroll($refs.target)"></Btn>
      <Btn @click.native="$smoothScroll($refs.target, 1000, -100)">100px</Btn>
    </demo-item>
    <demo-item name="default" description="" :code="top_code">
      <Btn @click.native="$smoothScroll()"></Btn>
      <Btn @click.native="$smoothScroll(0, 200)">200ms</Btn>
    </demo-item>
  </demo>
</template>

<style lang="stylus" scoped>
  @import '../../style/';
</style>

<script>
  export default {
    data () {
      return {
        code: `
<Btn @click.native="$smoothScroll(300)">300</Btn>
        `,
        ele_code: `
<Btn @click.native="$smoothScroll($refs.target)"></Btn>
<Btn @click.native="$smoothScroll($refs.target, 1000, -100)">100px</Btn>
        `,
        top_code: `
<Btn @click.native="$smoothScroll()"></Btn>
<Btn @click.native="$smoothScroll(0, 200)">200ms</Btn>
        `
      }
    }
  }
</script>



